import Vue from 'vue'

const LOGIN_URL = 'http://localhost:8000/api/users/login'
const REGISTER_URL = 'http://localhost:8000/api/users/register'

export default {
  login (ctx, creds, redirect) {
    Vue.post(LOGIN_URL, creds, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      emulateJSON: true,
      emulateHTTP: true
    })
    .then((res) => {
      if (res.status !== 200) {
        return
      }
    })
    .catch((err) => {
      ctx.data.error = err
    })
  },

  register (ctx, creds, redirect) {
    Vue.http.post(REGISTER_URL, creds, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'X-XSRF-TOKEN': Vue.cookie.get('XSRF-TOKEN')
      },
      emulateJSON: true,
      emulateHTTP: true
    })
    .then((res) => {
      console.log(res)
    })
    .catch((err) => {
      console.log(err)
    })
  },

  logout () {
    console.log('logout')
  },

  checkAuth () {
    return this.user
  }
}



